import { useEffect, useState } from "react";
import Chart from "react-google-charts";

import { GetMonthlyData } from "../../apis/GetMonthlyData";

export default function MonthlyChart() {
  const [data, setData] = useState([
    ["Month", "", "", ""],
    [0, 0, 0, 0],
  ]);

  const [year, setYear] = useState(new Date().getFullYear());

  const options = {
    series: {
      0: { type: "line", targetAxisIndex: 1, color: "#2FFB6A" },
      1: { type: "bars", targetAxisIndex: 0, color: "#E14F61" },
      2: { type: "bars", targetAxisIndex: 0, color: "#32B2CF" },
    },
    title: "Month",
    backgroundColor: "#0C320C",
    pointSize: 7,
    titleTextStyle: {
      color: "white", //  
    },
    hAxis: {
      // 
      textStyle: {
        fontSize: "20px",
        color: "white",
      },
      baselineColor: "white", //   
    },
    vAxis: {
      // 
      textStyle: {
        fontSize: "20px",
        color: "white",
      },
      gridlines: {
        //  
        color: "#8898A1",
      },
      baselineColor: "8898A1", //  
    },
    legend: {
      // 
      textStyle: {
        fontSize: "20px",
        color: "white",
      },
    },
    width: "100%",
    height: "100%",
    animation: { duration: 700, easing: "inAndOut", startup: true },
  };

  const yearHandler = (e) => {
    setYear(e.target.value);
  };

  useEffect(() => {
    const MonthlyData = async () => {
      const getData = await GetMonthlyData(year);
      setData(getData);
    };

    MonthlyData();
  }, [year]);

  return (
    <div className="monthlyChartSection">
      <h2>{`${year}   `}</h2>
      <input
        className="monthlyYearHandleInput"
        type="number"
        value={year}
        onChange={(e) => yearHandler(e)}
      />
      <Chart
        className="chartCSS"
        chartType="ComboChart"
        data={data}
        options={options}
      />
    </div>
  );
}
